<template>
  <div class = "button-list">
    <span
    v-for="(item,index) in btnList"
    :key="index"
    @click = buttonAction(item)
    >
        {{item.name}}
    </span>
  </div>
</template>

<script>
export default {
props: {
    btnList: []
},
methods: {
    buttonAction(item) {
      this.$emit("action", item.action)
    }
}
}
</script>

