<template>
  <div class="choice-patient">
    <div
        v-for="(item,index) in patientList"
        :key="index"
        @click="buttonAction(item)"
        class="patient-card"
    >
        <div class="modal_header patient-head">
            {{item.head}}
        </div>
        <div>
            <img :src="getImage(index)" alt="choice patient">
        </div>
        <div class="patient-text">
            {{item.text}}
        </div>
    </div>
  </div>

</template>

<script>
export default {
    props: {
        patientList: []
    },
    methods: {
        getImage(index) {
            return require(`@/assets/images/${this.patientList[index].image}`)
        },
        buttonAction(item) {
            this.$emit("action", item.action)
        }
    }
}
</script>

<style>
    .choice-patient {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    .patient-card {
        width: 300px;
        background-color: rgba(255, 255, 255, 1);
        display: flex;
        flex-direction: column;
        text-align: center;
        cursor: pointer;
        -webkit-box-shadow: 0px 0px 8px 0px rgba(34, 60, 80, 0.2) inset;
        -moz-box-shadow: 0px 0px 8px 0px rgba(34, 60, 80, 0.2) inset;
        box-shadow: 0px 0px 8px 0px rgba(34, 60, 80, 0.2) inset;
    }
    
    .patient-head {
        background-color: rgba(255, 255, 255, 0) !important;
    }
    .patient-text {
        padding: 10px;
        font-weight: 600;
    }
</style>